@import 'normalize.css';
:root {
  --primary: #fe2c55;
  --black: #000;
  --white: #fff;
  --text-color: #161823;
  --default-layout-height: 60px;
  --default-layout-width: 1150px;
  --default-layout-horizontal-spacer: 24px;
  --font-family: 'ProximaNova', sans-serif;
  --background-dark: rgba(18, 18, 18, 1);
  --background-gray: rgba(255, 255, 255, 0.12);
  --text-white: rgba(255, 255, 255, 0.9);
  --text-gray: rgba(22, 24, 35, 0.5);
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-family);
  font-size: 1.6rem;
  line-height: 1.5;
  text-rendering: optimizeLegibility;
  overflow-y: overlay;
}

//scrollbar
html *::-webkit-scrollbar {
  border-radius: 0;
  width: 8px;
}

html *::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(22, 24, 35, 0.06);
}

html *::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0);
}

button,
input,
[tabindex] {
  border: none;
  outline: none;
}
a {
  color: var(--text-color);
}

input[type='range' i] {
  appearance: none;
}

@font-face {
  font-family: ProximaNova;
  src: url('/assests/fonts/Proxima-Nova-Regular.woff2');
  font-weight: 400;
}

@font-face {
  font-family: ProximaNova;
  src: url('/assests/fonts/Proxima-Nova-Semibold.woff2');
  font-weight: 600;
}

/* vietnamese */
@font-face {
  font-family: 'IBM Plex Sans';
  font-style: normal;
  font-weight: 600;
  font-display: swap;
  src: url('/assests/fonts/zYX9KVElMYYaJe8bpLHnCwDKjWr7AIFsdA.woff2');
}

//custom tippy tooltip

body {
  .tippy-box {
    background-color: rgba(84, 84, 84, 0.92);
    font-size: 16px;
    border-radius: 8px;
    font-weight: 600;
    line-height: 22px;
  }
  .tippy-arrow {
    color: rgba(84, 84, 84, 0.92);
  }
  .tippy-content {
    padding: 8px 9px;
  }
}
.row {
  display: flex;
}
.col {
  display: flex;
  flex-direction: column;
}
.mt-24 {
  margin-top: 24px;
}
.mg_24_0 {
  margin: 24px 0;
}
.none-select {
  user-select: none;
}
