.wrapper {
    padding: 21px 30px;
}
.creator-form {
    display: flex;
    align-items: flex-end;
}
.input-container {
    border: 1px solid;
    border-color: rgba(22, 24, 35, 0.2);
    border-radius: 8px;
    flex-grow: 1;
    padding: 10px 9px;
    position: relative;
    background-color: rgba(22, 24, 35, 0.06);
}

.input-comment {
    outline: none;
    background-color: transparent;
    border: none;
    caret-color: var(--primary);
    color: var(--black-text);
    display: block;
    overflow-y: hidden;
    padding: 1px 0;
    resize: none;
    height: 18px;
    width: calc(100% - 40px);
}
.comment_btn {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    background-color: transparent;
    color: var(--primary);
    font-size: inherit;
    height: 39px;
    width: 48px;
    font-size: 14px;
    cursor: pointer;
}

.emoji-btn {
    align-items: center;
    border-radius: 8px;
    bottom: 0;
    cursor: pointer;
    display: flex;
    font-size: 22px;
    height: 32px;
    justify-content: center;
    margin: 3px;
    position: absolute;
    right: 9px;
    width: 32px;
    &:hover {
        background-color: rgba(22, 24, 35, 0.12);
    }
}
.emoji-box {
    position: absolute;
    top: -10px;
    right: 0;
    transform: translate(0, -100%);
}

.dark {
    .input-container {
        background-color: hsla(0, 0%, 100%, 0.122);
        border-color: hsla(0, 0%, 100%, 0.2);
    }
    .input-comment {
        color: #ffffffe6;
    }
    .emoji-btn:hover {
        background-color: hsla(0, 0%, 100%, 0.12);
    }
    .emoji-icon {
        fill: #fff;
    }
}
