.wrapper {
    display: flex;
    align-items: center;
    padding: 6px 16px;
    cursor: pointer;
    text-decoration: none;

    &:hover {
        background: rgba(22, 24, 35, 0.03);
    }
}

.avatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    object-fit: cover;
}

.info {
    flex: 1;
    margin-left: 12px;
    display: flex;
    flex-direction: column;
}

.name {
    font-size: 16px;
    font-weight: 500;
}
.check {
    margin-left: 8px;
    color: rgb(32, 213, 236);
}
.username {
    font-size: 14px;
    color: rgba(22, 24, 35, 0.5);
}
.dark {
    .username,
    .name {
        color: rgba(255, 255, 255, 0.9);
    }
    &:hover {
        background: rgba(255, 255, 255, 0.04);
    }
}
